
import React from 'react';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';


function useCaseCreator () {
    return (
        <form>
            <h3>Use Case erstellen</h3>
            <label > Name:
                <input type="textfield" />
            </label>
            <br></br>
            <label > Inhaltliche Beschreibung:
                <input type="textfield" />
            </label>
            <br></br>
            <label > Reichweite:
                <input type="Textfield" />
            </label>
            <br></br>
            <h3>Hier soll man aus allenden bestehenden Merkmalen auswählen müssen</h3>
            <label> Merkmal wählen
            <Select 
                id="demo-simple-select-helper"
                value={"churn_score"}>
                    <MenuItem value={10}>churn_score</MenuItem>
                    <MenuItem value={10}>churn_flag=red</MenuItem>
                    <MenuItem value={10}>Merkmal abc</MenuItem>
            </Select>
            </label>
        </form>
    );
}

export default useCaseCreator;