import React from 'react';
import Nav from './nav';
import './App.css';
import EnhancedTable from './EnhancedTable';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Merkmale from './Merkmale';
import UseCases from './UseCases';
import MerkmalCreator from './MerkmalCreator';
import useCaseCreator from './useCaseCreator';


function App() {
  return (
    <Router >
      <div className="App">
        <Nav />
        <Switch>
          <Route path="/data" component={Merkmale}/>
          <Route path="/UseCase" component={UseCases}/>
          <Route path="/" exact component={Merkmale}/>
          <Route path="/createMerkmal" component={MerkmalCreator} />
          <Route path="/createUseCase" component={useCaseCreator} />
        </Switch>
      </div>
    </Router >
  );
}

export default App;
