import React from 'react';

function MerkmalCreator () {
    return (
        <form>
            <h3>Merkmal erstellen</h3>
            <label > Name:
                <input type="textfield" />
            </label>
            <br></br>
            <label > Value:
                <input type="textfield" />
            </label>
            <br></br>
            <br></br>
            <label> Reichweite:
                <input type="Textfield" />
            </label>
            <h3>Idee: hier entsteht dass Frontend um einen Call an die DB der DDC Kollegen zu schicken um ein Merkmal aus deren Datenbank zu ziehen, dieses wird danach in der DB dieser Web App gespeichert und regemläßig aktualisiert</h3>
        </form>
    );
}

export default MerkmalCreator;