import React from 'react';
import EnhancedTable from './EnhancedTable';
import Button from '@material-ui/core/Button';
import CustomizedTables from './Table';


function UseCases() {
    return(
        <div className="Cases">
            <br></br>
            <Button href="/createUseCase" color="secondary" variant="contained">Create UseCase</Button>
            <div >
                <h3>Use Case in Progress:</h3>
                <CustomizedTables />
            </div>
            <div >
                <h3>Finished Use Cases:</h3>
                <CustomizedTables />
            </div>
        </div>
       
    );
}

export default UseCases;